.album__container {
  display: flex;
  padding: 20px;
  align-items: center;
  background: #FFFFFF;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.05), 0px 1px 4px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}

.album__logo {
  width: 64px;
  height: 64px;
  cursor: pointer;
}

.album__textInfo {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  min-width: 212px;
  justify-content: flex-start;
  margin-left: 20px;

  .album__title {
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    color: #404040;
  }

  .album__text {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: #404040;
  }
}

.album__more {
  cursor: pointer;
  align-self: flex-start;
  margin-left: 20px;
  position: relative;
  width: 24px;
  height: 24px;
}

.dropdownContent {
  position: absolute;
  top: 35px;
  right: 0;
  background-color: #FFFFFF;
  min-width: 180px;
  padding: 8px 0;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.05), 0px 8px 20px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  z-index: 100;
}

.dropdownContent div {
  padding: 12px 20px;
  display: block;
  cursor: pointer;

  color: #404040;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
}

.dropdownContent div:hover {
  background-color: #F2F2F2;
}